@import url("https://fonts.googleapis.com/css?family=Alegreya+Sans&display=swap");
:root {
  --bumblebee: #ffff8c;
  --sombra: #3c5369;
}
body {
  font-family: "Alegreya Sans", sans-serif;
  text-align: center;
  background-color: var(--sombra);
}
.nav {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  background-color: var(--sombra);
  position: fixed;
  top: 0;
  z-index: 1;
}
a {
  text-decoration: none;
  color: black;
}

.menu-link {
  background-color: slategrey;
  border-radius: 5px;
  padding: 5px;
  margin-bottom: 1px;
  border: 2px white solid;
}

.non-dropdown {
  margin: 1%;
}

.dropdown {
  position: relative;
  display: inline-block;
  margin: 1%;
  border-radius: 5px;
}

.hide {
  display: none;
  position: absolute;
  background-color: var(--bumblebee);
  width: 200px;
  border-radius: 5px;
}

.menu-link:hover {
  color: var(--bumblebee);
  background-color: var(--sombra);
}
.show {
  display: flex;
  position: absolute;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--bumblebee);
  width: 200px;
  border-radius: 5px;
}

.show div {
  width: 95%;
  background-color: white;
  text-align: center;
  line-height: 200%;
  margin: 1px;
  color: black;
  border-radius: 3px;
}

.show div:hover {
  background-color: var(--bumblebee);
}

.show a {
  width: 95%;
  background-color: white;
  text-align: center;
  line-height: 200%;
  margin: 1px;
  color: black;
  border-radius: 3px;
}

.show a:hover {
  background-color: var(--bumblebee);
}

.page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.title {
  color: var(--bumblebee);
}